import { Directive, ContentChild, HostListener } from '@angular/core';
import { Menu } from 'primeng/primeng';


export class MenuCloseOnScroll {

	 public menu: Menu;

	constructor() {
	}

	
	onWindowScroll() {
		this.closeMenu();
	}

	private closeMenu():void{
		if (this.menu && typeof this.menu.hide !== "undefined"){
			this.menu.hide();
		}
	}
static decorators: DecoratorInvocation[] = [
{ type: Directive, args: [{
	selector: '[kMenuCloseOnScroll]',
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'menu': [{ type: ContentChild, args: [Menu, ] },],
'onWindowScroll': [{ type: HostListener, args: ["window:scroll", [], ] },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



