import { Component, EventEmitter, Input, Output, OnInit } from '@angular/core';


export class DetailInfoComponent implements OnInit {

     label: string;
     value: string;
     valueField: string;
     link: string;
     tooltip: string;
     toolTipAsHTML: boolean;
     iconStyle: string;
     separator: string = "|";
     maxItemWidth: number = 300;
     isLastItem: boolean = false;

     itemClick = new EventEmitter<any>();

    public _data : any;

    public _setData(data : any)
    {
        this._data = data;
    }

    public isClickable: boolean = false;

    constructor() {
    }

    ngOnInit() {
        this.isClickable = this.itemClick.observers.length > 0;
    }

    onClick($event: MouseEvent){
        if (this.isClickable){
            this.itemClick.emit($event);
        }
    }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
    selector: 'kDetailInfo',
    templateUrl: './detail-info.component.html',
    styleUrls: ['./detail-info.component.scss']
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'label': [{ type: Input },],
'value': [{ type: Input },],
'valueField': [{ type: Input },],
'link': [{ type: Input },],
'tooltip': [{ type: Input },],
'toolTipAsHTML': [{ type: Input },],
'iconStyle': [{ type: Input },],
'separator': [{ type: Input },],
'maxItemWidth': [{ type: Input },],
'isLastItem': [{ type: Input },],
'itemClick': [{ type: Output },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



