

import { KalturaObjectBase } from '../kaltura-object-base';
import { KalturaTypesFactory } from '../kaltura-types-factory';

export class KalturaDropFolderErrorCode extends KalturaObjectBase {
    private _value : string;
    constructor( value?:string | number){
        super();
        this._value = value + '';
    }

    equals(obj : this) : boolean
    {
        return obj && obj.toString() === this._value;
    }

    toString(){
        return this._value;
    }

    static contentMatchPolicyUndefined = new KalturaDropFolderErrorCode('6');
	static dropFolderAppError = new KalturaDropFolderErrorCode('5');
	static errorAutenticate = new KalturaDropFolderErrorCode('2');
	static errorConnect = new KalturaDropFolderErrorCode('1');
	static errorGetDbFileList = new KalturaDropFolderErrorCode('4');
	static errorGetPhisicalFileList = new KalturaDropFolderErrorCode('3');
}
KalturaTypesFactory.registerType('KalturaDropFolderErrorCode',KalturaDropFolderErrorCode);


